import share from '@/components/share';
import uniSteps from "@/components/uni-steps/uni-steps.vue"

export default{
	components: {
		share,
		uniSteps
	},
	data() {
		return {
			banner: 'http://img30.360buyimg.com/cf/jfs/t1/18661/17/13907/142385/5ca33f27E9297c88a/dde91c6b10427bf2.jpg',
			specClass: 'none',
			specSelected:[],
			
			favorite: true,
			shareList: [],
			imgList: [
				{
					src: 'http://img30.360buyimg.com/cf/jfs/t1/18661/17/13907/142385/5ca33f27E9297c88a/dde91c6b10427bf2.jpg'
				},
				{
					src: 'https://gd3.alicdn.com/imgextra/i3/0/O1CN01IiyFQI1UGShoFKt1O_!!0-item_pic.jpg_400x400.jpg'
				},
				{
					src: 'https://gd3.alicdn.com/imgextra/i3/TB1RPFPPFXXXXcNXpXXXXXXXXXX_!!0-item_pic.jpg_400x400.jpg'
				},
				{
					src: 'https://gd2.alicdn.com/imgextra/i2/38832490/O1CN01IYq7gu1UGShvbEFnd_!!38832490.jpg_400x400.jpg'
				}
			],
			storyImgList: [
				{
					src: 'https://img30.360buyimg.com/cf/jfs/t1/27591/27/12625/417914/5c9af94fEa67a752d/007048fab20c14bb.jpg!q70.jpg.dpg'
				},
				{
					src: 'https://img30.360buyimg.com/cf/jfs/t1/31870/15/7990/437160/5c9af95bEd7da4100/979e09a7c4c7d779.jpg!q70.jpg.dpg'
				},
				{
					src: 'https://img30.360buyimg.com/cf/jfs/t1/31243/23/6890/504758/5c923c1fEadaf6cf6/c815342bc6740710.jpg!q70.jpg.dpg'
				},
				
			],
			specList: [
				{
					id: 1,
					name: '尺寸',
				},
				{	
					id: 2,
					name: '颜色',
				},
			],
			specChildList: [
				{
					id: 1,
					pid: 1,
					name: 'XS',
				},
				{
					id: 2,
					pid: 1,
					name: 'S',
				},
				{
					id: 3,
					pid: 1,
					name: 'M',
				},
				{
					id: 4,
					pid: 1,
					name: 'L',
				},
				{
					id: 5,
					pid: 1,
					name: 'XL',
				},
				{
					id: 6,
					pid: 1,
					name: 'XXL',
				},
				{
					id: 7,
					pid: 2,
					name: '白色',
				},
				{
					id: 8,
					pid: 2,
					name: '珊瑚粉',
				},
				{
					id: 9,
					pid: 2,
					name: '草木绿',
				},
			]
		};
	},
	async onLoad(options){
		
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		this.$api.msg(`点击了${id}`);
		
		
		//规格 默认选中第一条
		this.specList.forEach(item=>{
			for(let cItem of this.specChildList){
				if(cItem.pid === item.id){
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		this.shareList = await this.$api.json('shareList');
	},
	methods:{
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid){
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
			
		},
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		toFavorite(){
			this.favorite = !this.favorite;
		},
		stopPrevent(){},
		confirmOrder(){
			uni.navigateTo({
				url:'/pages/order/confirmation'
			})
		}
	},

}
